// cssExpr css
let cssExpr = new RegExp("\\.css");
let nHead = document.head || document.getElementsByTagName("head")[0];
// `onload` WebKit < 535.23 Firefox < 9.0 
let isOldWebKit = +navigator.userAgent.replace(/.*(?:AppleWebKit|AndroidWebKit)\/?(\d+).*/i, "$1") < 536;

// node
function isReady(node) {
  return node.readyState === "complete" || node.readyState === "loaded";
}

// loadCss css
function loadCss(url, setting, callback) {
  let node = document.createElement("link");

  node.rel = "stylesheet";
  addOnload(node, callback, "css");
  node.async = true;
  node.href = url;

  nHead.appendChild(node);
}

// loadJs js
function loadJs(url, setting, callback) {
  let node = document.createElement("script");

  node.charset = "utf-8";
  addOnload(node, callback, "js");
  node.async = !setting.sync;
  node.src = url;

  nHead.appendChild(node);
}

// webkitloadsheet
function pollCss(node, callback) {
  let isLoaded;

  if (node.sheet) {
    isLoaded = true;
  }

  setTimeout(function () {
    if (isLoaded) {
      // callback 
      callback();
    } else {
      pollCss(node, callback);
    }
  }, 20);
}

// onload
// 
function addOnload(node, callback, type) {
  let supportOnload = "onload" in node;
  let isCSS = type === "css";

  // webkitfirefox
  if (isCSS && (isOldWebKit || !supportOnload)) {
    setTimeout(function () {
      pollCss(node, callback);
    }, 1);
    return;
  }

  if (supportOnload) {
    node.onload = onload;
    node.onerror = function (e) {
      node.onerror = null;
      //window._cdnFallback(node);
      // eslint-disable-next-line no-console
      if (type == "css") {
        console.error("css" + node.href, e);
      }
      // eslint-disable-next-line no-console
      else {
        console.error("js" + node.src, e);
      }
      onload();
    };
  } else {
    node.onreadystatechange = function () {
      if (isReady(node)) {
        onload();
      }
    };
  }

  function onload() {
    // 
    node.onload = node.onreadystatechange = null;

    node = null;

    callback();
  }
}

// loadCssloadJs
function loadItem(url, list, setting, callback) {
  // url
  if (!url) {
    setTimeout(function () {
      onFinishLoading();
    });
    return;
  }

  if (cssExpr.test(url)) {
    loadCss(url, setting, onFinishLoading);
  } else {
    loadJs(url, setting, onFinishLoading);
  }

  // list
  // 
  function onFinishLoading() {
    let urlIndex = list.indexOf(url);
    if (urlIndex > -1) {
      list.splice(urlIndex, 1);
    }

    if (list.length === 0) {
      callback();
    }
  }
}

function doInit(list, setting, callback) {
  let cb = function () {
    callback && callback();
  };

  list = Array.prototype.slice.call(list || []);

  if (list.length === 0) {
    cb();
    return;
  }

  for (let i = 0, len = list.length; i < len; i++) {
    loadItem(list[i], list, setting, cb);
  }
}

// 
// 
// load
function ready(node, callback) {
  if (isReady(node)) {
    callback();
  } else {
    // 1500ms load
    let timeLeft = 1500;
    let isExecute = false;
    window.addEventListener("load", function () {
      if (!isExecute) {
        callback();
        isExecute = true;
      }
    });

    setTimeout(function () {
      if (!isExecute) {
        callback();
        isExecute = true;
      }
    }, timeLeft);
  }
}

// Loader
// async, sync
// async 
// sync  js
let Loader = {
  async: function (list, callback) {
    ready(document, function () {
      doInit(list, {}, callback);
    });
  },

  sync: function (list, callback) {
    ready(document, function () {
      doInit(
        list,
        {
          sync: true,
        },
        callback
      );
    });
  },
};

//window.Loader = Loader;

export { Loader };
